import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";
//createAsyncThunk()        :      ,  ,     (thunk), ,   ,    pending/fulfilled/rejected   ;
 //    
export const fetchBase = createAsyncThunk( //AsyncThunk -  
  "base/fetchBaseStatus",                   
  async () => {
    const { data } = await axios(
      `https://636fc33ebb9cf402c81f2d17.mockapi.io/baza`
    );//console.log(data)
    return data;
  });

const baseSlise = createSlice({
  name: "base",
  initialState: {
    items: [],  //   
    loading: 'pending',//'idle' | 'pending' | 'succeeded' | 'failed' // idle -
    categories: 0, //  
    background: 0,
  },
  reducers: { 
    setCategory(state, action){ //  
      state.categories = action.payload
     },

    setBackground(state){ //  
      if(state.background<7)
      state.background = state.background + 1;
      else(state.background = 0);
     },
   },
  extraReducers:{
    [fetchBase.pending]: (state)=>{
       state.loading = 'pending';
       state.items=[];   // console.log('   ')
    },
    [fetchBase.fulfilled]: (state, action)=>{
       state.loading = 'succeeded';
       state.items=action.payload; // console.log('   ')
    },
    [fetchBase.rejected]: (state, action)=>{
       state.loading = 'failed';
       state.items=[];
       console.log('  ')
    },
  },
});
export const { setCategory, setBackground } = baseSlise.actions;
export default baseSlise.reducer;
